export default {
	data() {
		return {};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	methods: {
		onSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：', e.detail.value);
			this.$Fetch({
				url: 'user/edit',
				data: e.detail.value,
				debug: true,
				success: res => {
					uni.showToast({
						title: '修改成功',
						icon: 'success'
					});
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						});
					}, 1500);
				}
			});
		}
	}
};
